body{
    scroll-behavior: smooth;
    /* transition:all ease-in-out 3s; */
}

/* === NAVBAR === */

.logo{
    height: 70%;
    width: 70%;
    margin-left: 75%;
    align-items: center;
}

.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: transparent;
    box-shadow: 0 1px 4px rgba(146, 161, 176, .15);
}

.nav {
    height: 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
    background-color: #d0e1f0;
}

.nav__menu {
    position: fixed;
    top: -0;
    right: -35%;
    width: 80%;
    padding: 1rem;   
    transition: 0.5s;
}

.nav_l {
    margin: 2%;
    color:rgba(0, 0, 0, 0.711);
    text-decoration: none;
}

.nav_l:hover{
    color: gray;
}

.button{
    color: #ffffff;
    background-color: #1169b2;
    border-radius: 3px;
    border: solid white 0px;
    align-items: center;
    height: 30px;
    width: 80px;
    transition: 0.5s;;
}
.button:hover{
    background-color: rgb(129, 203, 228);
}

/* ==== HOME - 1 === */

.home1{
    background-color: #d0e1f0;
    display: flex;
    height: 550px;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-top: 4rem;
}

.home1-content1{
    margin-left: 13rem;
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
}

.home1-content1 > h1{
    padding: 0px;
    margin: 0px;
    font-size: 80px;
    color:#1169b2;
}
.home1-content1 sup{
    font-size: 20px;
}
.h1-c2{
    font-size: 1rem;
    color:gray;
}

.home1-button{
    background-color: #5abd7c;
    color: #fff;
    height: 3rem;
    width: 11rem;
    border: 0px;
    border-radius: 0.2rem;
    margin-top: 1rem;
}

.home1-img{
    margin-right: 10rem;
}

